@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,300&family=Oswald:wght@500&family=Ubuntu:ital,wght@1,300&display=swap);
:root {
  --primary-app-color: #02203c;
  --secondary-app-color: #001528;
  --standout-color: #fffb00;
}
.App {
}
* {
  padding: 0;
  margin: 0;
}
.herosection {
  background: linear-gradient(
    -90deg,
    #02203c,
    #001528);
  background: linear-gradient(
    -90deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  );
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 65%, 51% 100%, 0 70%);
          clip-path: polygon(0 0, 100% 0, 100% 65%, 51% 100%, 0 70%);
}
.nav-items {
  cursor: pointer;
  color: #fff;
  font-size: 1.3rem;
  font-family: Ubuntu;
}
.brand-name {
  font-size: Oswald;
  color: #fff;
  font-size: 2rem;
}
.px-10 {
  padding-inline: 90px;
}
.herosection-2 {
  padding-top: 60px;
}
.Im-text-section {
  padding-top: 52px;
  text-align: center;
}
.Im-text {
  font-family: Ubuntu;
  color: #fff;
  font-weight: 700;
  font-size: 1.4rem;
}
.name-Im-text {
  color: #fffb00;
  color: var(--standout-color);
  font-size: 28px;
}
.Im-text-enthusiatic {
  font-family: "Open sans";
  color: #fff;
  font-size: 2rem;
}
.Im-text-subheading {
  font-family: "Open sans";
  color: #fff;
  font-size: 1.4rem;
}
.btn-hire-me {
  font-size: 1.3rem;
  font-family: Ubuntu;
  color: #fff;
  background: transparent;
  border-radius: 25px;
  margin-inline: 12px;
  padding: 5px 40px;
  border: 2px solid white;
  transition: all 0.3s ease-in-out;
  -webkit-animation: Anim 1s ease-in-out infinite;
          animation: Anim 1s ease-in-out infinite;
}
.btn-hire-me:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  background-color: #fffb00;
  background-color: var(--standout-color);
  color: #1c1c1c;
}
.btn-resume-me {
  font-size: 1.3rem;
  font-family: Ubuntu;
  color: black;
  background: pink;
  border-radius: 25px;
  margin-inline: 12px;
  padding: 2px 30px;
  border: 2px solid white;
  transition: all 0.3s ease-in-out;
}
.btn-resume-me:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
@-webkit-keyframes Anim {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes Anim {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.custom-img {
  width: 300px;
  border-radius: 50%;
  object-fit: contain;
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  border: 1px solid white;
}

